import { registerBlockType } from '@wordpress/blocks';
import {
	InspectorControls,
	useBlockProps,
} from '@wordpress/block-editor';

import {
	ToggleControl,
	SelectControl,
	PanelBody,
} from '@wordpress/components';

const { __ } = wp.i18n;

registerBlockType('filter-plus/woo-filter', {
    title: 'WooCommerce Product Filter',
    icon: 'image-filter',
    category: 'text',
	attributes: {
		colors: {
			type: 'boolean',
		},
		size: {
			type: 'boolean',
		},
		template: {
			type: 'integer',
			default: 1 ,
		},
		categories: {
			type: 'array',
			default: [] ,
		},
	},

	edit( { attributes, setAttributes } ) {
        const blockProps = useBlockProps();

        const {
            colors,
            size,
            template,
            categories,
        } = attributes;

        function onChangeDisplayColor( newValue ) {
            setAttributes( { colors: newValue } );
        }
		function onChangeDisplaySize( newValue ) {
            setAttributes( { size: newValue } );
        }
        function onChangeCatList( newValue ) {
            setAttributes( { categories: newValue } );
        }
        function onChangeTemplate( newValue ) {
            setAttributes( { template: newValue } );
        }
		function getCategories() {
			const options = [];
			tsGlobal?.woo_categories?.forEach((cat) => {
					options.push({ value: cat.term_id, label: cat.name });
			});

			return options;
		}

        return (
			<>
				<InspectorControls>
					<PanelBody title={ __( 'Settings', 'filter-plus' ) }>
						<SelectControl
							label={ __( 'Select Template:', 'filter-plus' ) }
							value={ template }
							options={ [
								{ value: 1 , label: __( 'Template-1', 'filter-plus' ) },
								{ value: 2 , label: __( 'Template-2', 'filter-plus' ) },
							] }
							onChange={ onChangeTemplate }
						/>
						<SelectControl
							multiple
							label={ __( 'Category List:', 'filter-plus' ) }
							value={ categories }
							options={getCategories()}
							onChange={ onChangeCatList }
						/>
						<ToggleControl
							label={ __( 'Display Colors:', 'filter-plus' ) }
							checked={ colors }
							onChange={ onChangeDisplayColor }
						/>
						<ToggleControl
							label={ __( 'Display Size:', 'filter-plus' ) }
							checked={ size }
							onChange={ onChangeDisplaySize }
						/>
					</PanelBody>
				</InspectorControls>

				<div { ...blockProps }>
					This is the editor text!
				</div>
			</>
		);
	},

	save( { attributes } ) {
		const {
            colors,
            size,
            template,
            categories,
		} = attributes;

		const blockProps = useBlockProps.save();

		return (
			<div {...blockProps}>
			</div>
		);
	},

} );
